export default {
  props: ['value'],
  data() {
    return {
      folderName: null,
    }
  },
  computed: {
    modalOpen: {
      get: function () {
        return this.value
      },
      set: function (nv) {
        this.$emit('input', nv)
      },
    },
  },
  methods: {
    close() {
      this.$emit('input', false)
    },
    createFolder() {
      this.$axios
        .post(`/api/folder/${this.$auth.user.username}/`, {
          name: this.folderName,
        })
        .then((r) => {
          this.$emit('addFolder', r.data)
          this.$emit('input', false)
        })
        .catch((e) => {
          this.$message.error('폴더 생성에 실패하였습니다. 다시 시도해주세요')
        })
    },
  },
}
